.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/* trick to increase specificity without !important */
.trash.trash.trash.trash.trash.trash.trash.trash.trash.trash {
  color: red;
  float: right;
  margin-top: 15px;
}

.details {
  float: left;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.content {
  margin-left: 40px;
}

.image {
  float: left;
  margin-top: 1vh;
}
.list-head {
  margin-top: 50px;
}

.add-button {
  float: right;
}
.delete-modal-text {
  color: red;
  margin-left: 1vw;
  margin-right: 1vw;
  text-align: center;
  margin-bottom: calc(25px + 5vw);
}

.modal-header {
  margin-top: 1vw;
}

.delete-modal-content {
  margin-bottom: 10px;
}

.subtext {
  color: black;
  font-weight: bold;
}

.modal {
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  left: 0;
  right: 0;
  margin-left: 9vw;
  margin-right: 9vw;
  outline-style: ridge;
}

.left.left {
  margin-left: 2vw;
}

.right.right {
  float: right;
  margin-right: 2vw;
}
.details-card.details-card.details-card {
  margin-top: 50px;
}

.home-button {
  text-align: center;
  margin-bottom: 10px;
}
